import React, { FormEvent } from 'react';

import { inject, observer } from 'mobx-react';

import { Row, Form, Icon, Input, Button, Col, Modal } from 'antd';

import { RouteComponentProps } from 'react-router-dom';
import { FormComponentProps } from 'antd/lib/form';
import { StoreType } from '@/store/store';

import { login } from '@/service/login';

import { TIME_COUNT_DOWN } from '@/utils/constant';

import './style.scss';

const { confirm } = Modal;

export type LoginPagePropType = Pick<StoreType, 'userStore' | 'homepageStore'> &
	RouteComponentProps &
	FormComponentProps;

export type LoginPageStateType = Readonly<{
	second: number;
	loading: boolean;
	disabled: boolean;
}>;

@inject((allStore: StoreType) => ({
	userStore: allStore.userStore,
	homepageStore: allStore.homepageStore,
}))
@observer
class LoginPage extends React.Component<LoginPagePropType, LoginPageStateType> {
	time: any = null;

	readonly state: LoginPageStateType = {
		second: TIME_COUNT_DOWN,
		loading: false,
		disabled: false,
	};

	componentDidMount(): void {
		const { userStore, history, homepageStore } = this.props;
		login({ userStore, history, homepageStore, isLoginPage: true });
	}

	handleSubmit = (e: FormEvent) => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			if (!err) {
				const { userStore, history, homepageStore } = this.props;
				// 
				login({
					userStore,
					history,
					homepageStore,
					isLoginPage: true,
					token: 'login',
				});
			}
		});
	};

	handleGetCode = () => {
		// 
		const email = this.props.form.getFieldValue('email');
		// 
		const emailReg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
		if (emailReg.test(email)) {
			this.setState({
				loading: true,
			});
			const _this = this;
			// 
			const isR = false;
			if (isR) {
				_this.handleGetCodeRequest();
			} else {
				confirm({
					title: '',
					content: '',
					okText: '',
					cancelText: '',
					onOk() {
						// 
						_this.handleGetCodeRequest();
					},
					onCancel() {
						console.log('Cancel');
					},
				});
			}
			return;
		}
		this.props.form.setFields({
			email: {
				value: '',
				errors: [new Error('')],
			},
		});
	};

	handleGetCodeRequest = () => {
		// 
		// loading: false
		// 
		// 
		setTimeout(() => {
			this.setState({
				loading: false,
				disabled: true,
			});
			this.time = setInterval(() => {
				const { second } = this.state;
				if (second) {
					this.setState({
						second: second - 1,
					});
				} else {
					window.clearInterval(this.time);
					this.setState({
						disabled: false,
						second: TIME_COUNT_DOWN,
					});
				}
			}, 1000);
		}, 5000);
	};

	get label() {
		const { second, disabled } = this.state;
		if (disabled) {
			return `${second}s`;
		}
		return '';
	}

	componentWillUnmount(): void {
		window.clearInterval(this.time);
	}

	render() {
		const { getFieldDecorator } = this.props.form;
		const { loading, disabled } = this.state;
		const { label } = this;

		return (
			<Row align="middle" justify="center" type="flex" className="login-page">
				<Col>
					<Form onSubmit={this.handleSubmit} className="login-page_form">
						<Form.Item>
							{getFieldDecorator('email', {
								rules: [{ required: true, message: '!' }],
							})(
								<Input
									prefix={<Icon type="mail" className="login-page_icon" />}
									placeholder=""
								/>,
							)}
						</Form.Item>
						<Form.Item>
							<Row gutter={8}>
								<Col span={14}>
									{getFieldDecorator('password', {
										rules: [{ required: true, message: '!' }],
									})(
										<Input
											prefix={<Icon type="lock" className="login-page_icon" />}
											placeholder=""
										/>,
									)}
								</Col>
								<Col span={10}>
									<Button
										block={true}
										onClick={this.handleGetCode}
										loading={loading}
										disabled={disabled}>
										{label}
									</Button>
								</Col>
							</Row>
						</Form.Item>
						<Form.Item>
							<Button type="default" htmlType="submit" block={true}>
								
							</Button>
						</Form.Item>
					</Form>
				</Col>
			</Row>
		);
	}
}

const WrappedLoginPage = Form.create({ name: 'normal_login' })(LoginPage);

export default WrappedLoginPage;
